import React, { useEffect, useState } from 'react';
import Papa from 'papaparse';
import './App.css';

function App() {
  const [data, setData] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [sortConfig, setSortConfig] = useState({ key: null, direction: 'ascending' });

  useEffect(() => {
    fetch(`${process.env.PUBLIC_URL}/msr_summer_league_2025.csv`)
      .then(response => response.text())
      .then(csvText => {
        Papa.parse(csvText, {
          header: true,
          skipEmptyLines: true,
          complete: (results) => {
            setData(results.data);
          },
        });
      });
  }, []);

  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value.toLowerCase());
  };

  const sortedData = [...data].sort((a, b) => {
    if (sortConfig.key) {
      const valA = a[sortConfig.key];
      const valB = b[sortConfig.key];

      const isNumeric = !isNaN(parseFloat(valA)) && isFinite(valA);

      if (isNumeric) {
        return (parseFloat(valA) - parseFloat(valB)) * (sortConfig.direction === 'ascending' ? 1 : -1);
      } else {
        return valA.localeCompare(valB) * (sortConfig.direction === 'ascending' ? 1 : -1);
      }
    }
    return 0;
  });

  const filteredData = sortedData.filter(row =>
    Object.values(row).some(val =>
      val?.toString().toLowerCase().includes(searchTerm)
    )
  );

  const handleSort = (key) => {
    setSortConfig(prev => ({
      key,
      direction: prev.key === key && prev.direction === 'ascending' ? 'descending' : 'ascending'
    }));
  };

  const renderCell = (key, val) => {
    if (key === 'MSR') {
      return <td className="highlight-msr">{val}</td>;
    }
    return <td>{val}</td>;
  };

  return (
    <div className="App">
      <h1> MSR Leaderboard  2025 NBA Summer League</h1>
      <input
        type="text"
        placeholder="Search by player name..."
        onChange={handleSearchChange}
        className="search-input"
      />
      {filteredData.length > 0 ? (
        <div className="table-container">
          <table className="data-table">
            <thead>
              <tr>
                {Object.keys(filteredData[0]).map(key => (
                  <th key={key} onClick={() => handleSort(key)}>
                    {key} {sortConfig.key === key ? (sortConfig.direction === 'ascending' ? '' : '') : ''}
                  </th>
                ))}
              </tr>
            </thead>
            <tbody>
              {filteredData.map((row, idx) => (
                <tr key={idx}>
                  {Object.entries(row).map(([key, val]) => renderCell(key, val))}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      ) : (
        <p>Loading data...</p>
      )}
    </div>
  );
}

export default App;






